import React, { Component } from 'react';

import CheckboxTree from 'react-checkbox-tree';
import 'react-checkbox-tree/lib/react-checkbox-tree.css';

import './App.css';
import { DATA } from './material-data.js';

const NODES = DATA.map(p => {
    return {
        value: p._id,
        label: p.name,
        // icon: <span/>,
        children: p.web_materials.map(wm => { return { value: wm._id, label: wm.name }; }),
    };
});

class App extends Component {
  constructor() {
    super();
    this.state = {
      checked: [],
      expanded: [],
    };
    console.log(NODES);
  }

  render() {
    return (
      <div className="App">
        <CheckboxTree
          nodes={ NODES }
          checked={ this.state.checked }
          expanded={ this.state.expanded }
          onCheck={ checked => { console.log("checked", checked); this.setState({ checked }); } }
          onExpand={ expanded => { console.log("expanded", expanded); this.setState({ expanded }); } }
          />
      </div>
    );
  }
}

export default App;
